import dayjs from "dayjs";
const api = async (url, query) => {
    const inputRow = `${url}${query}`;
    const input = encodeURI(inputRow);
    const response = await fetch(input);
    return response;
};
const getFeed = async (rssUrl) => {
    const response = await api("https://8080-cs-994772306133-default.asia-east1.cloudshell.dev/rss-feed/", `?url=${rssUrl}`);
    const feed = {
        ok: response.ok,
        status: response.status,
        statusText: response.statusText,
        url: "",
        title: "",
        description: "",
        link: "",
        contents: [],
    };
    const urlParse = new URL(response.url);
    feed.url = urlParse.searchParams.get("url");
    if (!response.ok)
        return feed;
    const result = await response.json();
    feed.title = result.title;
    feed.description = result.description;
    feed.link = result.link;
    const contents = result.contents.map((content) => (Object.assign(Object.assign({}, content), { date: dayjs(content.isoDate) })));
    feed.contents = contents;
    return feed;
};
export const getFeeds = async (feedUrls) => {
    const promises = feedUrls.map((feedUrl) => getFeed(feedUrl));
    const feeds = await Promise.all(promises);
    return feeds;
};
